<template>
  <div>
    <el-table
      :data="cEstimateDetailsTableData"
      border
      :summary-method="getSummaries"
      show-summary
      v-loading="loading"
      size="mini"
      :header-cell-style="{ 'text-align': 'center' }"
      :cell-style="{ 'text-align': 'right' }"
      style="width: 100%; margin-top: 20px"
      :row-style="TableRowStyle"
    >
      <template slot="empty">
        <slot name="empty"></slot>
      </template>
      <el-table-column type="index" label="No." width="50"> </el-table-column>
      <el-table-column prop="product_name" label="商品名">
        <template slot-scope="scope">
          <el-autocomplete
            v-if="scope.row.isEdit"
            popper-class="my-autocomplete"
            v-model="scope.row.product_name"
            :fetch-suggestions="querySearch"
            placeholder="商品"
            style="width: 100%"
            @select="handleSelect"
          >
            <i
              class="el-icon-edit el-input__icon"
              slot="suffix"
              @click="handleIconClick"
            ></i>
            <template slot-scope="{ item }">
              <div class="name">{{ (item.value = item.product_name) }}</div>
              <span class="category">カテゴリ：{{ item.category1 }}</span>
            </template>
          </el-autocomplete>
          <span v-else>{{ scope.row.product_name }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="price"
        label="単価"
        width="200"
        :formatter="stateFormat"
      >
        <template slot-scope="scope">
          <el-input v-if="scope.row.isEdit" v-model="price"></el-input>
          <span v-else>{{ scope.row.price }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="quantity" label="数量" width="100">
        <template slot-scope="scope">
          <el-input
            v-if="scope.row.isEdit"
            v-model="scope.row.quantity"
          ></el-input>
          <span v-else>{{ scope.row.quantity }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="sub_total"
        label="金額"
        width="200"
        :formatter="stateFormat"
      >
      </el-table-column>
      <el-table-column
        prop="tax"
        label="消費税"
        width="200"
        :formatter="stateFormat"
      >
      </el-table-column>
      <el-table-column label="操作" width="100">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            v-if="!scope.row.isEdit"
            @click="edit(scope.row, scope.$index)"
            >編集</el-button
          >
          <el-button
            type="text"
            size="small"
            v-if="scope.row.isEdit"
            @click="save(scope.row)"
            >保存</el-button
          >
          <el-button
            type="text"
            size="small"
            v-if="scope.row.isEdit"
            @click="cancel(scope.row, scope.$index)"
            >キャンセル
          </el-button>
          <el-button
            type="text"
            size="small"
            v-if="scope.row.isEdit"
            @click="handleDelete(scope.$index, scope.row)"
            >删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button
      @click="add()"
      style="width: 100%"
      v-if="!this.cInvoiceForm"
      plain
      >追加</el-button
    >
  </div>
</template>

<script>
import {
  postEstimateDetailsData,
  putEstimateDetailsData,
  deleteEstimateDetail,
} from "@/api/estimates";
import { Message } from "element-ui";

export default {
  name: "EstimateDetailsTable",
  props: {
    cEstimateDetailsTableData: {
      type: Array,
      default() {
        return [];
      },
    },
    cProductsDataAll: {
      type: Array,
      default() {
        return [];
      },
    },
    cEstimateCode: {
      type: String,
      default() {
        return "";
      },
    },
    cMainFlag: {
      type: String,
      default() {
        return "";
      },
    },
    cInvoiceForm: {
      type: String,
      default() {
        return "";
      },
    },
  },
  data() {
    return {
      tableData: [],
      price: "",
      loading: false,
    };
  },
  methods: {
    sub_total(row) {
      return row.price * row.quantity;
    },
    tax(row) {
      return row.price * row.quantity * 0.1;
    },
    total(row) {
      return this.sub_total(row) + this.tax(row);
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "総計";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 2) {
            sums[index] = "";
          } else if (index === 3) {
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+$)/g, "$1,");
            sums[index] += "点";
          } else if (index === 4) {
            sums[index] = ((sums[index] * 110) / 100)
              .toString()
              .replace(/(\d)(?=(\d{3})+$)/g, "$1,");
            sums[index] = "合計:  ¥" + sums[index];
          } else if (index === 5) {
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+$)/g, "$1,");
            sums[index] = "消費税:  ¥" + sums[index] + "(10%)";
          } else {
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+$)/g, "$1,");
            sums[index] += "円";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    newProjectcode(param) {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();

      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var currentdate =
        param + year + month + strDate + hours + minutes + seconds;

      return currentdate;
    },
    postEstimateDetailsData(row) {
      let values = {};
      if (this.price) {
        row.price = this.price;
      }
      values["estimate_code"] = this.cEstimateCode;
      values["estimate_details_code"] = this.newProjectcode("Esd");
      values["ProductCode"] = row.ProductCode;
      values["product_name"] = row.product_name;
      values["quantity"] = row.quantity;
      values["price"] = row.price;
      values["sub_total"] = this.sub_total(row).toString();
      values["tax"] = this.tax(row).toString();
      values["total"] = this.total(row).toString();
      values["main_flag"] = this.cMainFlag;
      values["created_by"] = this.$store.getters.full_name;
      postEstimateDetailsData(values)
        .then((res) => {
          console.log(res);
          if (res.data.status === 1) {
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
            this.price = "";
            this.$emit("estimatedetdatareload");
          } else {
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    putEstimateDetailsData(row) {
      let values = {};
      if (this.price) {
        row.price = this.price;
      }
      // values['estimate_code'] = this.cEstimateCode
      values["estimate_details_code"] = row.estimate_details_code;
      values["ProductCode"] = row.ProductCode;
      values["product_name"] = row.product_name;
      values["quantity"] = row.quantity;
      values["price"] = row.price;
      values["sub_total"] = this.sub_total(row).toString();
      values["tax"] = this.tax(row).toString();
      values["total"] = this.total(row).toString();
      values["main_flag"] = row.main_flag;
      values["modified_by"] = this.$store.getters.full_name;
      putEstimateDetailsData(values)
        .then((res) => {
          console.log(res);
          if (res.data.status === 1) {
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
            this.price = "";
            this.$emit("estimatedetdatareload");
          } else {
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleDelete(index, row) {
      this.loading = true;
      let values = {};
      values["estimate_details_code"] = row.estimate_details_code;
      values["deleted_by"] = this.$store.getters.full_name;
      deleteEstimateDetail(values)
        .then((res) => {
          if (res.data.status === 1) {
            this.loading = false;
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
            // 向父组件发送重新获取数据请求
            this.$emit("estimatedetdatareload");
          } else {
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    stateFormat(row, column, cellValue) {
      cellValue += "";
      if (!cellValue.includes(".")) cellValue += ".";
      return cellValue
        .replace(/(\d)(?=(\d{3})+\.)/g, function ($0, $1) {
          return $1 + ",";
        })
        .replace(/\.$/, "");
    },
    // 新增
    add() {
      this.cEstimateDetailsTableData.push({
        product_name: "",
        quantity: "",
        price: "",
        tax: "",
        total: "",
        isEdit: true,
        isAdd: true,
      });
    },
    // 编辑
    edit(row) {
      // 备份原始数据
      row["oldRow"] = JSON.parse(JSON.stringify(row));
      this.price = row.price;
      row.isEdit = true;
    },
    //保存
    save(row) {
      if (row.isAdd) {
        this.postEstimateDetailsData(row);
      } else {
        this.putEstimateDetailsData(row);
      }
      row.isEdit = false;
    },
    // 取消
    cancel(row, index) {
      // 如果是新增的数据
      if (row.isAdd) {
        this.tableData.splice(index, 1);
      } else {
        // 不是新增的数据  还原数据
        for (const i in row.oldRow) {
          row[i] = row.oldRow[i];
        }
      }
    },
    querySearch(queryString, cb) {
      var cProductsDataAll = this.cProductsDataAll;
      var results = queryString
        ? cProductsDataAll.filter(this.createFilter(queryString))
        : cProductsDataAll;
      // 调用 callback 返回建议列表的数据

      // console.log(this.detailsForm.price)
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        // 从头开始匹配=== 0,全字段匹配> -1
        return (
          restaurant.product_name
            .toLowerCase()
            .indexOf(queryString.toLowerCase()) > -1
        );
      };
    },
    handleSelect(item) {
      // 将选择的商品信息回传给data
      console.log(item);
      this.price = item.price;
      console.log(this.price);
    },
    handleIconClick(ev) {
      console.log(ev);
    },
    TableRowStyle({ row }) {
      let rowBackground = {};
      if (row.main_flag === true) {
        rowBackground.background = "pink";
        return rowBackground;
      } else if (row.main_flag === false) {
        rowBackground.background = "Ivory";
        return rowBackground;
      }
      return "";
    },
  },
};
</script>

<style scoped>
.name {
  text-overflow: ellipsis;
  overflow: hidden;
}
.category {
  font-size: 12px;
  color: #b4b4b4;
}

.highlighted .category {
  color: #ddd;
}
</style>
